export class Deque {
    constructor() {
        this._data = new Array();
    }
    get size() {
        return this._data.length;
    }
    at(i) {
        return this._data[i];
    }
    empty() {
        return this._data.length == 0;
    }
    front() {
        if (!this.empty()) {
            return this._data[0];
        }
        else {
            return undefined;
        }
    }
    back() {
        if (!this.empty()) {
            return this._data[this._data.length - 1];
        }
        else {
            return undefined;
        }
    }
    pushBack(elem) {
        this._data.push(elem);
    }
    pushFront(elem) {
        this._data.unshift(elem);
    }
    popBack() {
        return this._data.pop();
    }
    popFront() {
        return this._data.shift();
    }
    clear() {
        this._data.length = 0;
    }
}
