import kEvent from "./kEvent";
import Utils from "./Utils";
export class System {
}
System.FPS = 60;
System.SPF = 1 / System.FPS;
export class Vector3 extends kEvent {
    constructor(x = 0, y = 0, z = 0) {
        super();
        this.reserve = null;
        this._x = x;
        this._y = y;
        this._z = z;
    }
    set x(x) { this._x = x; this.trigger(kEvent.Types.change, this._x, this._y, this._z); }
    set y(y) { this._y = y; this.trigger(kEvent.Types.change, this._x, this._y, this._z); }
    set z(z) { this._z = z; this.trigger(kEvent.Types.change, this._x, this._y, this._z); }
    get x() { return this._x; }
    get y() { return this._y; }
    get z() { return this._z; }
    set(x, y, z) { this._x = x; this._y = y; this._z = z; return this.trigger(kEvent.Types.change, this._x, this._y, this._z); }
}
export class Vector2 extends kEvent {
    constructor(x = 0, y = 0) {
        super();
        // for holding necessary reference
        this.reserve = null;
        this._x = x;
        this._y = y;
    }
    set x(x) { this._x = x; this.trigger(kEvent.Types.change, this._x, this._y); }
    set y(y) { this._y = y; this.trigger(kEvent.Types.change, this._x, this._y); }
    get x() { return this._x; }
    get y() { return this._y; }
    set(x, y) { this._x = x; this._y = y; return this.trigger(kEvent.Types.change, this._x, this._y); }
    copy(v) { this._x = v.x; this._y = v.y; return this.trigger(kEvent.Types.change, this._x, this._y); }
    clear() { this._x = 0; this._y = 0; return this.trigger(kEvent.Types.change, this._x, this._y); }
    iadd(...vs) { vs.forEach((v) => { this._x += v.x; this._y += v.y; }); return this.trigger(kEvent.Types.change, this._x, this._y); }
    isub(...vs) { vs.forEach((v) => { this._x -= v.x; this._y -= v.y; }); return this.trigger(kEvent.Types.change, this._x, this._y); }
    imul(...ms) { ms.forEach((m) => { this._x *= m; this._y *= m; }); return this.trigger(kEvent.Types.change, this._x, this._y); }
    idiv(...ms) {
        ms.forEach((m) => {
            if (!Utils.equals(m, 0)) {
                this._x /= m;
                this._y /= m;
            }
            else {
                throw new RangeError("0 cannot be a divisor");
            }
        });
        return this.trigger(kEvent.Types.change, "both", this._x, this._y);
    }
    icomponentProduct(...vs) { vs.forEach((v) => { this._x *= v.x; this._y *= v.y; }); return this.trigger(kEvent.Types.change, this._x, this._y); }
    inormalize() {
        let l = this.magnitude();
        if (Utils.equals(l, 0)) {
            return this.clear();
        }
        else if (l > 0) {
            return this.idiv(l);
        }
    }
    clone() { return new Vector2(this.x, this.y); }
    add(...vs) { let x = this.x, y = this.y; vs.forEach((v) => { x += v.x; y += v.y; }); return new Vector2(x, y); }
    sub(...vs) { let x = this.x, y = this.y; vs.forEach((v) => { x -= v.x; y -= v.y; }); return new Vector2(x, y); }
    mul(...ms) { let x = this.x, y = this.y; ms.forEach((m) => { x *= m; y *= m; }); return new Vector2(x, y); }
    div(...ms) {
        let x = this.x, y = this.y;
        ms.forEach((m) => {
            if (!Utils.equals(m, 0)) {
                x /= m;
                y /= m;
            }
            else {
                throw new RangeError("0 cannot be a divisor");
            }
        });
        return new Vector2(x, y);
    }
    scalarProduct(v) { return this.x * v.x + this.y * v.y; }
    vectorProduct(v) { return this.x * v.y - v.x * this.y; }
    componentProduct(...vs) { let x = this.x, y = this.y; vs.forEach((v) => { x *= v.x; y *= v.y; }); return new Vector2(x, y); }
    magnitude() { return Math.sqrt(this.x * this.x + this.y * this.y); }
    normalize() {
        let l = this.magnitude();
        if (Utils.equals(l, 0)) {
            return new Vector2(0, 0);
        }
        else if (l > 0) {
            return this.div(l);
        }
    }
    bind(evName, evHandler) {
        kEvent.bind.call(this, evName, evHandler);
        return this;
    }
    unbind(evName, evHandler) {
        kEvent.unbind.call(this, evName, evHandler);
        return this;
    }
    change(handler) {
        this.unbind(kEvent.Types.change, handler);
        this.bind(kEvent.Types.change, handler);
    }
}
export class Point2D extends Vector2 {
    constructor(x, y) { super(x, y); }
}
export class Pair {
    constructor(l, r) { this.l = l; this.r = r; }
}
