function dispatch(obj, handlers, ...args) {
    for (let i = 0; i < handlers.length; ++i) {
        handlers[i].call(obj, ...args);
    }
}
export default class kEvent {
    constructor() {
        this.trigger = function (evName, ...args) {
            let scope = this;
            if (!scope._events) {
                return scope;
            }
            let evHandlers = scope._events[evName];
            if (evHandlers) {
                dispatch(scope, evHandlers, ...args);
            }
            return scope;
        };
        this.listen = function (obj, evName, evHandler) {
            let bound = this;
            if (obj) {
                let event = () => {
                    evHandler.apply(bound, evName, evHandler);
                };
                Object.assign(event, {
                    obj: obj,
                    name: evName,
                    handler: evHandler
                });
                obj.bind(evName, event);
            }
            return bound;
        };
        this.ignore = function (obj, evName, evHandler) {
            let scope = this;
            obj.unbind(evName, evHandler);
            return scope;
        };
    }
}
kEvent.Types = {
    change: "change"
};
kEvent.bind = function (evName, evHandler) {
    let scope = this;
    scope._events || (scope._events = {});
    let evList = scope._events[evName] || (scope._events[evName] = []);
    evList.push(evHandler);
    return scope;
};
kEvent.unbind = function (evName, evHandler) {
    let scope = this;
    if (!scope._events) {
        return scope;
    }
    if (!evName && !evHandler) {
        scope._events = {};
        return scope;
    }
    let evNames = evName ? [evName] : Object.keys(scope._events);
    evNames.forEach(evName => {
        let handlers = scope._events[evName];
        if (handlers) {
            let newHandlers = [];
            if (evHandler) {
                newHandlers = handlers.filter((handler) => {
                    handler = handler.handler ? handler.handler : handler;
                    return handler != evHandler;
                });
            }
            scope._events[evName] = newHandlers;
        }
    });
    return scope;
};
