import Two from "two.js";
export class Grid {
    constructor(w = 50, h = 50) {
        this._two = null;
        this._initialized = false;
        this.width = w;
        this.height = h;
    }
    init() {
        this._initialize();
        this._initSprite();
    }
    _initialize() {
        this._two = new Two({
            type: Two.Types.canvas,
            width: this.width,
            height: this.height
        });
        this._initialized = true;
    }
    _initSprite() {
        let two = this._two;
        let col = two.makeLine(two.width / 2, 0, two.width / 2, two.height);
        let row = two.makeLine(0, two.height / 2, two.width, two.height / 2);
        row.stroke = col.stroke = "#6dcff660";
        two.update();
        $(document.body).css({
            background: "url(" + two.renderer.domElement.toDataURL("image/png") + ") 0 0 repeat",
            backgroundSize: this.width + "px " + this.height + "px"
        });
    }
}
